<template>
    <div class="public-nav">
        <div class="flex-item logo"><img :src="logoSrc" alt="及物LOGO"></div>
        <div class="flex-item nav-box">
            <ul>
                <li v-for="(nav,nIndex) in navList" :key="nIndex" :class="{'active':active==nIndex}">
                    <p @click="jumpTo(nav.path,nIndex)">{{nav.text}}</p><i class="hot-icon" v-if="nIndex==3"></i>
                </li>
            </ul>
        </div>
        <div class="flex-item ">
            <div class="login-box" v-if="islogin">
                <ul>
                    <li @click="isShowUserNav=!isShowUserNav"><i class="userinfo"></i></li>
                    <li><i class="shopinfo"></i></li>
                </ul>
            </div>
            <div class="login-box" v-else>
                <ul>
                    <li @click="jumpTo('login')">登陆 </li>
                    <li> | </li>
                    <li> 注册</li>
                </ul>
            </div>
        </div>
        <div class="flex-item menu-button"><img :src="menuSrc" alt="菜单"></div>
        <div class="user-nav" v-if="isShowUserNav">
            <div>
                <div class="img-box">
                    <img src="../assets/images/logo_03@2x.png" alt="头像">
                </div>
                <div class="name-box">
                    <p>username</p>
                </div>
            </div>
            <ul>
                <li v-for="(uNav,uIndex) in userNav" :key="uIndex">
                    <p @click="jumpTo(uNav.path,uIndex);isShowUserNav=false">{{uNav.text}}</p>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
    name:'publicNav',
    data() {
        return {
            active:0,
            navList:[{
                text:'首页',
                path:'home'
            },{
                text:'艺术作品',
                path:'artwork'
            },{
                text:'艺术衍生品',
                path:'artstore'
            },{
                text:'我要创作',
                path:'artDIY'
            }],
            islogin:true,
            logoSrc:require("@/assets/images/logo_03@2x.png"),
            menuSrc:require("@/assets/images/menu@2x.png"),
            userNav:[{
                text:'个人中心',
                path:'userinfo'
            },{
                text:'我的订单'
            },{
                text:'我的收藏'
            },{
                text:'收获地址'
            },{
                text:'退出账号'
            }],
            isShowUserNav:false
        }
    },
    methods:{
        jumpTo(str,index){
            this.active = index;
            this.$router.push({name:str})
        }
    },
}
</script>
