<template>
  <div id="app">
    <publicNav v-if="!isLoginOrRegist" />
    <router-view/>
    <publicFooter v-if="!isLoginOrRegist" />
  </div> 
</template>
<script>
import publicNav from '@/components/publicNav.vue'
import publicFooter from '@/components/publicFooter.vue'

export default {
  data() {
    return {
      isLoginOrRegist:false,
    }
  },
  components: {
    publicNav,publicFooter
  },
  mounted() {
    // console.log(this.$route.name)
    if(this.$route.name=='login'||this.$route.name=="regist"){
      this.isLoginOrRegist = true;
    }
  },
}
</script>
