import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Login from './views/Login.vue'
import Artwork from './views/Artwork.vue'
import Artstore from './views/Artstore.vue'
import ArtstoreDetails from './views/ArtstoreDetails.vue'

import UserInfo from './views/UserInfo.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/artwork',
      name: 'artwork',
      component: Artwork
    },
    {
      path: '/artstore',
      name: 'artstore',
      component: Artstore
    },{
      path: '/artstoredetails/:id',
      name: 'artstoredetails',
      component: ArtstoreDetails
    },    
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/userinfo',
      name: 'userinfo',
      component: UserInfo
    },





    {
      path: '/about',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      // component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    }
  ]
})
